/**
 * 基于ColorUI封装的 Tabbar 组件
 */
export default {
	data() {
		return {
		}
	},
	props: {
		// Tab列表
		tabList: {
			type: Array,
			default: function() {
				return []
			}
		},
		// 当前选中Tab
		current: {
			type: Number,
			default: 0
		},
		// 选中文字颜色
		selectedTextColor: {
			type: String,
			default: 'text-green'
		},
		// 选项卡背景颜色
		backgroundColor: {
			type: String,
			default: 'bg-white'
		}
	},
	methods: {
		switchTab(index, item) {
			this.$emit('tab-changed', {index: index, item: item})
		}
	}
}
